import React, { Component } from "react";
import "./ServiciosCardIcon.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGem} from "@fortawesome/free-regular-svg-icons"
import { faShieldAlt,faCut, faMagic} from "@fortawesome/free-solid-svg-icons"

class ServiciosCardIcon extends Component {

    render() {
        var icono = {}
        const styles = {
            background: this.props.color
        }
        let styles2 

        if(this.props.icono==="magic"){
            icono= faMagic
        }
        else if (this.props.icono==="gem"){
            icono= faGem
        }
        else if (this.props.icono==="cut"){
            icono= faCut
        }
        else if (this.props.icono==="shield"){
            icono= faShieldAlt
        }
        else {
            icono= faShieldAlt
            styles2={ display: this.props.icono }
        }
        
        return(
            <div className="serviciosCardIcon p-4" style={styles}>
                <FontAwesomeIcon className="socialMedia fa-2x" icon={icono} style={styles2}/>
                <h3 className="servicioCardTitle text-white">{this.props.subtitulo}</h3>
                <p className="servicioCardParagraph">{this.props.parrafo}</p>
            </div>
        )
    }
}

export default ServiciosCardIcon